<template>
  <div class="context">
    <div class="content">
      <div class="table-container">
        <table id="grid-basic" class="filterable-table bootgrid-table">
          <thead>
          <tr>
            <th data-column-id="id" data-type="numeric">ID</th>
            <th data-column-id="email">Логин</th>
            <th data-column-id="password">Пароль</th>
            <th data-column-id="status">Статус</th>
            <th data-column-id="status">Редактировать</th>
          </tr>
          </thead>
          <tbody id="grid-basic-result">
          <tr v-for="account in accounts">
            <td>{{ account.id }}</td>
            <td>{{ account.email }}</td>
            <td>{{ account.password }}</td>
            <td>{{ Boolean(account.enabled) ? "активен" : "не активен"}}</td>
            <td v-on:click="editTalkyTime(account)"><i class="fas fa-edit"></i></td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import '@/external/jquery.dataTables.min.css'
import '@/external/jquery.dataTables.min.js'
import Connection from "@/rsocket/connection";
import Vue from "vue";
import router from "@/views/router/routes";

export default {
  name: "TalkyTimeList",
  methods: {
    editTalkyTime(account) {
      Vue.$log.debug(account)
      router.push("/dashboard/talkytime/edit/" + account.id)
    }
  },
  mounted() {
    Connection.talkyTimeList((res) => {
      let data = JSON.parse(res.data.toString());
      Vue.$log.debug(data);
      this.accounts = data.accounts;
    }).then(() => {
      $('#grid-basic').DataTable();
    });
  },
  data: () => {
    return {
      accounts: []
    }
  }
}
</script>

<style lang="scss" scoped>
.content {
  box-sizing: border-box;
  background: #222427;
  padding: 10px;
  margin: 10px;
  border-radius: 10px;
  color: white;
  text-align: center;
}

.table-container {
  background: #2c2f33;
  color: #fff;
}

.filterable-table {
  overflow: hidden;
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  text-align: center;
  background-color: #494e55;
  font-weight: bold;

  tbody tr {
    border-bottom: 2px solid #0000006c;
  }
}

@media screen and (max-width: 768px) {
  table.filterable-table thead {
    border: none;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }

  table.filterable-table tr {
    border-bottom: 1px solid #ddd;
    display: block;
    margin-bottom: 30px;
  }

  table.filterable-table td {
    display: block;
    text-align: right;
  }

  table.filterable-table td::before {
    content: attr(data-label);
    float: left;
    font-weight: bold;
    text-transform: uppercase;
  }

  table.filterable-table td:last-child {
    border-bottom: 0;
  }
}
</style>